.yt-block .corner:before,
.yt-block .corner:after {
  content: '';
  position: absolute;
  left: 18px;
  width: 17px;
  height: 56px;
  background: #000;
}
.yt-block .corner:before {
  top: -14px;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.yt-block .corner:after {
  bottom: -14px;
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
