.hide-nav #header .navbar-nav {
  display: none !important;
}
pre {
  padding: 0;
  border: none;
}
html {
  height: 100%;
}
body {
  font-size: 16px;
  line-height: 1.375;
  font-family: 'Gotham', sans-serif;
  min-width: 320px;
  color: #000;
  font-weight: 300;
}
button,
input[type="button"],
input[type="reset"],
input[type="submit"],
a {
  -webkit-appearance: none;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  -webkit-transition: all 0s ease 0s;
  -moz-transition: all 0s ease 0s;
  -o-transition: all 0s ease 0s;
  transition: all 0s ease 0s;
}
a:focus,
button:focus,
input:focus,
select:focus,
textarea:focus {
  outline: none !important;
}
h1 {
  font-size: 76px;
  line-height: 79px;
  font-weight: 500;
  margin: 0 0 20px;
}
h2 {
  font-size: 56px;
  line-height: 59px;
  font-weight: 500;
  margin: 0 0 20px;
}
h3 {
  font-size: 48px;
  line-height: 51px;
  font-weight: 500;
  margin: 0 0 20px;
}
h4 {
  font-size: 24px;
  line-height: 27px;
  font-weight: 500;
  margin: 0 0 20px;
}
h5 {
  font-size: 18px;
  line-height: 21px;
  font-weight: 500;
  margin: 0 0 20px;
}
h6 {
  font-size: 14px;
  line-height: 17px;
  font-weight: 500;
  margin: 0 0 20px;
}
#wrapper {
  overflow: hidden;
  min-height: 100vh;
  position: relative;
  padding: 120px 0 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}
@media only screen and (max-width: 767px) {
  #wrapper {
    padding-top: 58px;
  }
}
